/* -------------------------------------------------------------------------------------------------
  # Captions
------------------------------------------------------------------------------------------------- */
.wp-caption {
  max-width: 100%;

  img[class*="wp-image-"] {
    @include center-block;
  }

  .wp-caption-text {
    margin: 0.8075em 0;
  }
}

.wp-caption-text {
  text-align: center;
}
