/* -------------------------------------------------------------------------------------------------
  # Forms Fields
------------------------------------------------------------------------------------------------- */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
  padding: 0.25em;

  &:focus {
    color: $color_text-input-focus;
  }
}

select {
  border: 1px solid $color_border-input;
}

textarea {
  width: 100%;
}
