/* -------------------------------------------------------------------------------------------------
  # Media
------------------------------------------------------------------------------------------------- */
.page_content .wp-smiley,
.post_content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0;
}

iframe,
embed,
object,
video {
  max-width: 100%;
}

.embed {
  position: relative;
  height: 0;
  padding-bottom: 56.25%; // 16:9, use 75% for 4:3
  overflow: hidden;

  iframe,
  object,
  embed,
  video {
    position: absolute;
    width: 100%;
    height: 100%;
  }
}
